import React from 'react';
import {NavLink} from 'react-router-dom';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import logo1 from './images/logo1.png';

const Navbar = () =>{
  return(
    <>
    <div className="container-fluid nav_bg">
     <div className="row">
      <div className="col-10 mx-auto">

        <nav className="navbar navbar-expand-lg navbar-light bg-light">
    <div className="container-fluid">
        <NavLink className="navbar-brand" to="/"> <img src={logo1} alt="logo" width="120px" height="100vh"/> </NavLink>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" 
              aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
          <li className="nav-item">
            <NavLink activeClassName="menu_active" exact className="nav-link" aria-current="page" to="/">Home</NavLink>
          </li>

          <li className="nav-item">
            <NavLink activeClassName="menu_active" className="nav-link" to="/service">Services</NavLink>
          </li>
          <li className="nav-item">
            <NavLink activeClassName="menu_active" className="nav-link" to="/about">About</NavLink>
          </li>
          <li className="nav-item">
            <NavLink activeClassName="menu_active" className="nav-link" to="/contact">Contact</NavLink>
          </li>
          <li className="nav-item">
            <NavLink activeClassName="menu_active" className="nav-link" to="/login">Login <AccountCircleIcon /></NavLink>
          </li>
        </ul>
    </div>
    </div>
        </nav>
          </div>
        </div>
      </div>
    </>
  );
};

export default Navbar;